
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: 'date-range',
mixins: [],
props: {
startTime: {
type: String,
default: ''
},
endTime: {
type: String,
default: ''
}
},
data () {
return {
dateStartTime: '',
dateEndTime: ''
}
},
computed: {
},
components: {
Group,
Datetime
},
watch: {},
methods: {
setStartTime () {
this.$emit('changeDate', {
dateStartTime: this.dateStartTime,
dateEndTime: this.dateEndTime
})
},
setEndTime () {
this.$emit('changeDate', {
dateStartTime: this.dateStartTime,
dateEndTime: this.dateEndTime
})
}
},
created () {
this.dateStartTime = this.startTime
this.dateEndTime = this.endTime
},
mounted () {
}
}



