import VHeader from 'components/v-header/v-header'
import DateRange from 'base/date-range/date-range'
import ClassifyTabs from 'base/classify-tabs/classify-tabs'

export default {
  name: 'analysis-settings',
  mixins: [],
  props: {
  },
  data () {
    return {
      showFlag: false,
      startTime: '',
      endTime: ''
    }
  },
  computed: {
  },
  components: {
    VHeader,
    DateRange,
    ClassifyTabs
  },
  watch: {},
  methods: {
    show () {
      this.showFlag = true
    },
    hide () {
      this.showFlag = false
    },
    getTitle (label) {
      switch (label) {
        case 'company': {
          return '企业分析设置'
        }
        case 'product': {
          return '产品分析设置'
        }
        case 'tender': {
          return '招标分析设置'
        }
        default: {
          return ''
        }
      }
    }
  },
  created () {
  },
  mounted () {}
}



